import Vue from "vue";
import VueRouter from "vue-router";
import arHome from "../views/arHome.vue";
import enHome from "../views/enHome.vue";
import Notfound from "../views/404.vue";

Vue.use(VueRouter);

const routes = [

  { path: "/", name: "Home",component: arHome },
  { path: "/ar",name: "arHome",component: arHome },
  { path: "/en",name: "enHome",component: enHome },
  { path: "*",name: "*",component: Notfound },
];

const router = new VueRouter({
  mode: "history",  
  routes
});

export default router;
